<template>
  <el-dialog
    :class="$root.themeHomeChange === '1' ? 'msgDialog_light' : 'msgDialog'"
    title="提示"
    :visible.sync="dialogVisible"
    :modal="false">
    <!-- 内容可接收HTML字符串 -->
    <div v-html="content"></div>
    <!-- 尾部 -->
    <span slot="footer" class="dialog-footer">
      <el-button @click="closeDialog()">取 消</el-button>
      <el-button type="primary" @click="sureDialog()">确 定</el-button>
    </span>
  </el-dialog>
</template>


<script>
export default {
  components: {},
  props: {
    title: {
      type: String,
      default: () => {
        return '提示';
      },
    },
    content: {
      type: String,
      default: () => {
        return '此操作将 XXX ，是否继续？';
      },
    }
  },
  data() {
    return {
      dialogVisible: false, //弹框显隐
    };
  },

  watch: {},
  computed: {},

  methods: {
    /**
     * @description:打开弹框回调
     */
    openDialog() {
      this.dialogVisible = true;
    },

    /**
     * @description:关闭弹框回调
     */
    closeDialog() {
      this.dialogVisible = false;
      this.$emit('close');
    },

    /**
     * @description:确定弹框回调
     */
    sureDialog() {
      this.$emit('confirm')
      this.closeDialog();
    },
  },
};
</script>

<style lang="scss" scoped>
.msgDialog {
  /deep/ {
    /*修改添加规则弹窗 */
    .el-dialog {
      width: 27% !important;
      // height: 200px !important;
      background: rgba($color: #031a4a, $alpha: .9);
      border-radius: 5px;
      border: 1px solid #4389f9;
      margin-top: 38vh !important;
      
      .el-dialog__header {
        border-bottom: 1px solid #4389f9 !important;
        padding: 10px;
        .el-dialog__title {
          color: #FFF;
          font-size: 14px;
          font-weight: bold;
        }
      }
      
      .el-dialog__body {
        color: #FFF;
        font-size: 12px;
        min-height: 30px;
        display: flex;
        align-items: center;
        padding: 10px;
      }
      
      .el-dialog__footer {
        border-top: 1px solid #4389f9 !important;
        padding: 0;
  
        .dialog-footer {
          display: block;
          height: 100%;
          padding:5px;
          box-sizing: border-box;
          .el-button--default {
            background-color: #041338;
            color: #d3d3d3;
            border: 1px solid #2a5cb0;
            border-radius: 3px;
            text-align: center;
          }
          .el-button--primary {
            background-color: #4389F9;
            color: #ffffff;
            border-radius: 3px;
            text-align: center;
            transition: all .3s;
            &:hover {
              background-color: #041338;
              border: 1px solid #2a5cb0;
            }
          }
        }
      }
    }
  }
}

// 浅色主题
.msgDialog_light {
  /deep/ {
    /*修改添加规则弹窗 */
    .el-dialog {
      width: 27% !important;
      // height: 200px !important;
      background: rgba($color: #fff, $alpha: 1);
      border-radius: 5px;
      border: 1px solid #bbbbbb;
      margin-top: 38vh !important;
      
      .el-dialog__header {
        border-bottom: 1px solid #bbb !important;
        padding: 10px;
        .el-dialog__title {
          color: #333;
          font-size: 14px;
          font-weight: bold;
        }
      }
      
      .el-dialog__body {
        color: #333;
        font-size: 12px;
        min-height: 30px;
        display: flex;
        align-items: center;
        padding: 10px;
      }
      
      .el-dialog__footer {
        border-top: 1px solid #bbb !important;
        padding: 0;
  
        .dialog-footer {
          display: block;
          height: 100%;
          padding:5px;
          box-sizing: border-box;
          .el-button--default {
            background-color: #fff;
            color: #333;
            border: 1px solid #bbb;
            border-radius: 3px;
            text-align: center;
          }
          .el-button--primary {
            background-color: #4389F9;
            color: #ffffff;
            border-radius: 3px;
            text-align: center;
            transition: all .3s;
            &:hover {
              color: #4389F9;
              background-color: #fff;
              border: 1px solid #4389F9;
            }
          }
        }
      }
    }
  }
}
</style>